.facts {
  margin-top: 180px;
  & .wrapper {
    display: flex;
    max-width: 1840px;
    justify-content: center;
    align-items: center;
    padding: 0;
    @include media(tablet) {
      flex-direction: column;
    }
  }
  &--innovation {
    margin-top: 200px;

    & .wrapper {
      justify-content: flex-end;
    }
  }
  &__photo-container {
    display: flex;
    height: 100%;
    align-items: flex-start;
    flex: 0 1 50%;
    justify-content: center;
  }
  &__photo-element {
    &:nth-child(1):not(&--innovation) {
      align-self: flex-end;
      margin-bottom: -70px;
      margin-right: 10px;
    }
    &--innovation:nth-child(1) {
      margin-right: 30px;
      @include media(mobile) {
        margin-right: 10px;
      }
    }
    &--innovation:nth-child(2) {
      margin-top: -80px;
    }
  }

  &__description {
    display: flex;
    flex-wrap: wrap;
    flex: 0 1 50%;
    @include media(tablet) {
      flex: 1 1 100%;
      flex-direction: column;
    }
    &-container {
      max-width: 661px;
      margin-left: 150px;
      flex: 0 1 50%;
      @include media(tablet) {
        margin-left: 0;
        margin-top: 150px;
        padding: 0 30px;
        flex: 1 1 100%;
      }
      &--innovation {
        max-width: 536px;
        margin-left: 0;
        margin-right: 100px;
        flex: 0 1 50%;
        @include media(tablet) {
          margin-left: 0;
          margin-right: 0;
          margin-top: 80px;
          margin-bottom: 100px;
          order: 1;
        }
      }
    }
    &-heading {
      margin-top: 13px;
      font-family: 'Cairo', sans-serif;
      font-size: 36px;
      line-height: 40px;
      font-weight: 700;
      text-transform: uppercase;
      &--innovation {
        max-width: 200px;
      }
    }

    &-card {
      height: 172px;
      display: flex;
      flex-direction: column;
      flex: 0 1 190px;
      margin-top: 30px;
      margin-right: 30px;
    }
    &-text {
      margin-top: 22.5px;
      font-family: 'Open Sans', sans-serif;
      font-size: 14px;
      line-height: 24px;
      color: $color-dark-gray;
    }

    &-number {
      font-weight: 700;
      font-family: 'Caior', sans-serif;
      font-size: 12px;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 100%;
        height: 2px;
        display: block;
        background-color: #eee;
      }
      &::before {
        content: '';
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 8%;
        height: 2px;
        display: block;
        background-color: $color-gray;
        z-index: 999;
      }
    }
  }
  &__text-1 {
    margin-top: 45px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    line-height: 32px;
    color: #707070;
  }
  &__text-2 {
    max-width: 475px;
    margin-top: 30px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    line-height: 24px;
    color: #4e4e4e;
  }
}
